@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  object-fit: cover;
}

.header__wrapper {
  position: fixed;
  width: 100%;
  z-index: 2;
}

.header__nav--opened {
  display: block;
  position: absolute;
  top: 43px;
  left: 0;
  right: 0;
  z-index: 10;
  background-color: #ffffff;
}

.header__nav--closed {
  display: none;
}

.header__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.header__item {
  box-shadow: inset 0 0 2px #63758c;
}

.header__link {
  display: block;
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  padding-top: 10px;
  padding-bottom: 10px;
  cursor: pointer;
}
.header__link:hover {
  color: #56c3b2;
}

.header__button {
  position: absolute;
  top: 15px;
  right: 13px;
  border: 0;
  padding: 0;
  width: 21px;
  height: 20px;
  cursor: pointer;
  background-color: #000000;
  z-index: 3;
}

.header__nav--closed + .header__button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  background-image: url("../img/menu/toggle-close.svg");
  background-repeat: no-repeat;
}

.header__nav--opened + .header__button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  background-image: url("../img/menu/toggle-open.svg");
  background-repeat: no-repeat;
}

@media (min-width: 768px) {
  .header__button {
    display: none;
  }
  .header__nav {
    display: inline;
  }
  .header__list {
    display: flex;
  }
  .header__item {
    background-color: #000000;
    flex-grow: 1;
    width: 20%;
  }
  .header__link {
    font-size: 18px;
    color: #ffffff;
    font-weight: 400;
    text-decoration: underline;
  }
}
@media (min-width: 1150px) {
  .header__link {
    font-size: 22px;
    color: #ffffff;
    font-weight: 400;
    text-decoration: underline;
  }
}
.logo {
  display: block;
  font-size: 16px;
  line-height: 1.5;
  color: #ffffff;
  font-weight: 400;
  text-decoration: none;
  background-color: #000000;
  padding: 10px 0;
  width: 100%;
  cursor: pointer;
}
.logo:hover {
  color: #56c3b2;
}

@media (min-width: 768px) {
  .logo {
    font-size: 20px;
  }
}
.page__main {
  position: relative;
  top: 44px;
}

@media (min-width: 768px) {
  .page__main {
    top: 94px;
  }
}
.intro {
  height: 200px;
}

.intro__title {
  font-size: 18px;
  line-height: 1.5;
  width: 200px;
  margin: 0 auto;
  color: #ffffff;
  padding-top: 70px;
}

@media (min-width: 768px) {
  .intro__title {
    font-size: 22px;
    width: 250px;
  }
}
@media (min-width: 768px) {
  .intro {
    margin-bottom: 50px;
  }
  .intro__title {
    font-size: 32px;
    width: 850px;
  }
}
.services {
  max-width: 1400px;
  margin: 0 auto;
}

.services__title {
  margin: 0;
  padding-top: 20px;
  font-size: 20px;
  line-height: 1.5;
  color: #56c3b2;
  text-decoration: underline;
}

.services__list {
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  padding-top: 20px;
  display: flex;
  flex-wrap: wrap;
}

.services__item {
  margin: 0 auto;
  border: 1px solid #63758c;
  max-width: 280px;
  margin-bottom: 30px;
}

.services__wrapper-image {
  overflow: hidden;
  background-color: #63758c;
}

.services__link {
  display: block;
  width: 280px;
  height: 200px;
}
.services__link:hover .services__image {
  transition: transform 0.5s;
  transform: scale(1.1);
}

.services__list-title {
  font-size: 18px;
  line-height: 1.2;
  margin: 0;
  margin-bottom: 5px;
  padding-top: 10px;
  text-decoration: underline;
}

.services__list-info {
  display: flex;
  padding: 10px 20px;
  margin-bottom: 10px;
  justify-content: space-between;
}

.services__price {
  font-size: 16px;
  line-height: 1;
  font-weight: 400;
}

.services__value {
  font-size: 16px;
  line-height: 1;
  font-weight: 400;
}

.services__button {
  border: none;
  color: #ffffff;
  background-color: #63758c;
  margin-bottom: 20px;
  width: 200px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 15px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .services__title {
    font-size: 32px;
    padding: 0;
    margin-bottom: 50px;
  }
}
.advantages {
  max-width: 900px;
  margin: 0 auto;
}

.advantages__title {
  font-size: 20px;
  line-height: 1;
  margin: 0;
  margin-bottom: 30px;
  padding-top: 30px;
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 0 30px;
}

.advantages__item {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  text-align: justify;
  padding-left: 35px;
  position: relative;
  width: 260px;
  margin: 0 auto;
  margin-bottom: 20px;
  cursor: pointer;
}
.advantages__item:hover .advantages__icon {
  fill: #56c3b2;
}

.advantages__icon {
  position: absolute;
  top: 8px;
  left: -10px;
}

@media (min-width: 768px) {
  .advantages__title {
    font-size: 24px;
    margin-bottom: 50px;
  }
  .advantages__item {
    font-size: 18px;
  }
  .advantages__item:hover {
    transition: transform 0.5s;
    transform: scale(1.05);
  }
  .advantages__list {
    margin-bottom: 50px;
  }
}
.contacts__title {
  margin: 0;
  margin-bottom: 15px;
  font-size: 20px;
}

.contacts__text {
  margin: 0 auto;
  margin-bottom: 15px;
  font-size: 16px;
  line-height: 1.2;
  width: 250px;
  font-weight: 400;
}

.contacts__link {
  display: block;
  text-decoration: none;
  color: #ffffff;
  background-color: #63758c;
  width: 240px;
  margin: 0 auto;
  margin-bottom: 15px;
  font-size: 16px;
  text-transform: uppercase;
  padding: 10px 0;
  border-radius: 20px;
  cursor: pointer;
}
.contacts__link:hover {
  background-color: #56c3b2;
}

.contacts__map {
  display: block;
  margin: 0 auto;
  border: 1px solid #63758c;
  margin-bottom: 15px;
  width: 100%;
  max-width: 600px;
  min-height: 300px;
}

@media (min-width: 768px) {
  .contacts__title {
    font-size: 24px;
    margin-bottom: 50px;
  }
  .contacts__text {
    font-size: 18px;
    width: 400px;
    margin-bottom: 30px;
  }
  .contacts__link {
    display: inline-block;
    margin-bottom: 50px;
  }
  .contacts__link--number {
    margin-right: 30px;
  }
  .contacts__map {
    margin-bottom: 30px;
  }
}
.social__list {
  display: flex;
  list-style-type: none;
  width: 100px;
  justify-content: space-between;
  padding: 0;
  margin: 0 auto;
  margin-bottom: 10px;
}

.social__icon {
  fill: #56c3b2;
}

.social__link:hover .social__icon {
  fill: #63758c;
}